<script>
  import { onMount } from "svelte";

  export let copy;
  export let image;
  export let imageSelected;

  let modelViewer = "";
  let open = true;
  let animationFinished = true;

  onMount(() => {
    modelViewer = document.getElementById("unit01");
  });

  function playRange() {
    let animationPause;

    modelViewer.play();
    if (open & animationFinished) {
      animationFinished = false;
      removeHotspots();
      modelViewer.currentTime = "3";
      animationPause = setTimeout(function () {
        modelViewer.pause();
        open = false;
        animationFinished = true;
      }, 1000);
    } else if (!open && animationFinished) {
      animationFinished = false;
      modelViewer.currentTime = "5";
      animationPause = setTimeout(function () {
        modelViewer.pause();
        open = true;
        showHotspots();
        animationFinished = true;
      }, 600);
    }
  }

  function removeHotspots() {
    var liElements = document.getElementsByClassName("svelte-fakij8");
      for(var i = 0; i< liElements.length; ++i){
        liElements[i].style.opacity = 0.9;
        liElements[i].style.pointerEvents = 'none';
      }
    let hotspots = document.getElementsByClassName("hotspot");
    for (var i = 0; i < hotspots.length; i++) {
      hotspots[i].removeAttribute('data-visible');
    }
  }

  function showHotspots() {
    var liElements = document.getElementsByClassName("svelte-fakij8");
      for(var i = 0; i< liElements.length; ++i){
        liElements[i].style.opacity = 1;
        liElements[i].style.pointerEvents = '';
      }
    let hotspots = document.getElementsByClassName("hotspot");
    for (var i = 0; i < hotspots.length; i++) {
      hotspots[i].setAttribute('data-visible', '');
    }
  }
</script>

<style type="text/scss">.close-device {
  display: flex; }
  .close-device .img-container {
    width: 2.5rem; }
  .close-device .close-device-normal {
    color: #ffffff; }
  .close-device .close-device-selected {
    color: #1299d6; }
  .close-device .text {
    text-align: left;
    line-height: 1rem;
    margin-left: 0.8rem;
    width: 80px;
    align-self: center; }
</style>

<div class="close-device" id='closeBtn' on:click={playRange}>
  <div class="img-container">
    <img class="img" src={open ? image : imageSelected} alt="close-device" />
  </div>
  <div class="text">
    <p class={open ? 'close-device-normal' : 'close-device-selected'}>
      {@html open ? copy.close_device : copy.open_device}
    </p>
  </div>
</div>
