<script>
  import { onMount } from "svelte";

  export let headline = "";
  export let subheadline = "";
  export let arrow = "";

  function animation() {
    var animation_TL = new TimelineMax();
    animation_TL.add("init");
    animation_TL.from("h3", 3, { y: "-200rem", ease: "Circ.easeOut" }, "init");
    animation_TL.to("h3", 5, { opacity: 1, ease: "Circ.easeOut" }, "init");

    animation_TL.to(
      ".subheadline",
      2,
      { opacity: 1, ease: "Circ.easeInOut" },
      "init+=1.5"
    );
    animation_TL.to(
      ".down-arrow",
      1,
      { opacity: 0.6, ease: "Circ.easeInOut" },
      "init+=2"
    );

    animation_TL.add("initLoop");
    animation_TL.to(
      arrow01,
      1,
      { opacity: 0.8, ease: "Circ.easeInOut", repeat: -1, yoyo: true },
      "initLoop"
    );
    animation_TL.to(
      arrow02,
      1,
      { opacity: 0.6, ease: "Circ.easeInOut", repeat: -1, yoyo: true },
      "initLoop+=0.5"
    );
    animation_TL.to(
      arrow03,
      1,
      { opacity: 0.4, ease: "Circ.easeInOut", repeat: -1, yoyo: true },
      "initLoop+=1"
    );
  }

  onMount(() => {
    animation();
  });
</script>

<style type="text/scss">.header {
  padding: 0.9rem 0.9rem 0 0.9rem;
  line-height: 1rem;
  height: 13%; }
  .header h3 {
    color: #ffffff;
    font-size: 1rem;
    font-weight: lighter;
    opacity: 0; }
  .header .subheadline {
    margin-top: 0.4rem;
    color: #149ed9;
    opacity: 0; }
  .header .down-arrow {
    width: 2rem;
    display: block;
    margin: auto;
    margin-top: 0.5rem;
    opacity: 0; }
    .header .down-arrow #arrow01 {
      position: relative; }
    .header .down-arrow #arrow02 {
      position: relative;
      top: -0.7rem; }
    .header .down-arrow #arrow03 {
      position: relative;
      top: -1.4rem; }
</style>

<div class="header">
  <h3>{headline}</h3>
  <p class="subheadline">{subheadline}</p>
  <div class="down-arrow">
    <img id="arrow01" class="img arrow" src={arrow} alt="down arrow" />
    <img id="arrow02" class="img arrow" src={arrow} alt="down arrow" />
    <img id="arrow03" class="img arrow" src={arrow} alt="down arrow" />
  </div>
</div>
