<script>
  import { onMount, beforeUpdate, afterUpdate } from "svelte";
  import Footer from "./components/Footer.svelte";
  import Header from "./components/Header.svelte";
  import FeaturesList from "./components/FeaturesList.svelte";
  import Model from "./components/custom/3DModel.svelte";
  import FeaturesDescription from "./components/FeaturesDescription.svelte";
  import LoadingScreen from "./components/custom/LoadingScreen.svelte";
  import BlurryScreen from "./components/custom/BlurryScreen.svelte";
  import Legal from "./components/Legal.svelte";
  import ZoomGlass from "./components/custom/ZoomGlass.svelte";

  export let content;
  export let videos;

  let optionClicked = false;
  let featureId = -1;
  let modelViewer = "";
  let isModelVisible = false;
  let showLegal = false;
  let showBlurryScreen = false;
  let showAnimation = false;

  onMount(function () {
    modelViewer = document.getElementById("unit01");
  });

  function isVisible() {
    isModelVisible = true;
  }

  afterUpdate(() => {});

  function playRange(start, time) {
    let animationPause;

    modelViewer.play();
    modelViewer.currentTime = start;
    animationPause = setTimeout(function () {
      modelViewer.pause();
      open = false;
    }, time);
  }

  function openDescription(event) {
    var closeBtn = document.getElementById("closeBtn");
    closeBtn.style.opacity = 0.5;
    closeBtn.style.pointerEvents = "none";
    featureId = event.detail;
    optionClicked = true;
    let hotspot = document.getElementById("animation0" + (featureId + 1));
    hotspot.classList.add("hotspot-selected");
    modelViewer.cameraControls = false;
    clickHS(featureId);
    removeHotspots(featureId);
  }

  function closeDescription(event) {
    var closeBtn = document.getElementById("closeBtn");
    closeBtn.style.opacity = 1;
    closeBtn.style.pointerEvents = "";
    let hotspot = document.getElementById("animation0" + (featureId + 1));
    hotspot.classList.remove("hotspot-selected");
    featureId = -1;
    optionClicked = false;
    modelViewer.cameraControls = true;
    showAnimation = false;
    playRange(2, 100);
    showHotspots();
  }

  function removeHotspots(id) {
    let hotspots = document.getElementsByClassName("hotspot");
    for (var i = 0; i < hotspots.length; i++) {
      if (i != id) {
        hotspots[i].removeAttribute("data-visible");
      }
    }
  }

  function showHotspots() {
    let hotspots = document.getElementsByClassName("hotspot");
    for (var i = 0; i < hotspots.length; i++) {
      hotspots[i].setAttribute("data-visible", "");
    }
  }

  function openModalHandler(event) {
    showLegal = event.detail;
    showBlurryScreen = event.detail;
  }

  function closeModalHandler(event) {
    showLegal = event.detail;
    showBlurryScreen = event.detail;
  }

  function clickHS(el) {
    let model = modelViewer;
    showAnimation = false;
    model.resetTurntableRotation();
    modelViewer.cameraTarget = "0m 30m 0m";

    model.autoRotate = false;
    switch (el) {
      case 0:
        //Awake in a flash
        model.cameraOrbit = "33.36deg 85.73deg 130m";
        playRange(19, 2000);
        break;

      case 1:
        //Universal cable connectivity
        playRange(7, 2000);
        showAnimation = true;
        model.cameraOrbit = "2.405deg 75.05deg 130m";
        break;

      case 2:
        //Get thing done fast
        playRange(13, 2000);
        model.cameraOrbit = "0.9279deg 53.67deg 130m";
        break;

      case 3:
        //Unplug for longer
        playRange(2, 100);
        model.cameraOrbit = "4.247deg 86.1deg 130m";
        break;

      case 4:
        //Fast, realible connections
        playRange(23, 2000);
        model.cameraOrbit = "-32.6deg 80.2deg 130m";
        break;

      case 5:
        //Immersive viewing
        playRange(2, 100);
        model.cameraOrbit = "-80.405deg 80.0deg 130m";
        break;

      default:
        console.log("default");
    }
  }
</script>

<style type="text/scss">.main-container {
  text-align: center;
  margin: 0 auto;
  background: linear-gradient(90deg, #110f10 0%, #322b2d 100%);
  width: 100%;
  height: 100%;
  min-height: 600px; }
  .main-container .content-container {
    height: 71%;
    min-height: 490px;
    margin-bottom: 2%; }
  .main-container .features-container {
    width: 90%;
    height: 50%;
    margin: auto;
    max-width: 750px;
    display: flex;
    justify-content: center; }
</style>

<svelte:head>
  <script
    src="https://unpkg.com/@webcomponents/webcomponentsjs@2.1.3/webcomponents-loader.js">
  </script>
  <script
    src="https://unpkg.com/intersection-observer@0.5.1/intersection-observer.js">
  </script>
  <script
    src="https://unpkg.com/resize-observer-polyfill@1.5.1/dist/ResizeObserver.js">
  </script>
  <script src="https://unpkg.com/focus-visible@5.1.0/dist/focus-visible.js">
  </script>
</svelte:head>
<svelte:options accessors={true} />
<div class="main-container">
  {#if !isModelVisible}
    <LoadingScreen loader={content.assets.loader} />
  {/if}
  {#if showBlurryScreen}
    <BlurryScreen />
  {/if}
  {#if showLegal}
    <Legal
      exitButton={content.assets.exit_button}
      copy={content.footer.legal.description}
      on:close-modal={closeModalHandler} />
  {/if}
  <Header
    headline={content.header.headline}
    subheadline={content.header.subheadline_mobile}
    arrow={content.assets.down_arrow} />
  <div class="content-container">
    <Model
      {modelViewer}
      model={content.assets.model}
      lights={content.assets.lights}
      background={content.assets.background}
      checkmark={content.assets.checkmark}
      on:model-visible={isVisible}
      on:open-description={openDescription}>
      {#if showAnimation}
        <ZoomGlass
          zoomifyGlassFront={content.assets.zoomify_glassFront}
          zoomifyGlass={content.assets.zoomify_glass} />
      {/if}
    </Model>
    {#if isModelVisible}
      <div class="features-container">
        {#if !optionClicked}
          <FeaturesList
            features={content.hotspots}
            on:open-description={openDescription}
            listCheckmark={content.assets.list_checkmark}
            rightArrow={content.assets.right_arrow} />
        {:else}
          <FeaturesDescription
            title={content.hotspots[featureId].copy}
            subtitle={content.hotspots[featureId].description}
            videoAccount={videos[featureId].data_account}
            videoId={videos[featureId].video_id}
            thumbnail={content.hotspots[featureId].url.thumbnail}
            exitButton={content.assets.exit_button}
            on:close-description={closeDescription}
            model={modelViewer} />
        {/if}
      </div>
    {/if}
  </div>
  <Footer
    copy={content.footer}
    on:open-modal={openModalHandler}
    logo={content.assets.evo_checkmark}
    closeDevice={content.assets.close_device}
    openDevice={content.assets.open_device} />
</div>
