<script>
  import { createEventDispatcher } from "svelte";
  import VideoPlayer from "./custom/VideoPlayer.svelte";

  export let title = "";
  export let subtitle = "";
  export let videoAccount = "";
  export let videoId = "";
  export let model = "";
  export let thumbnail = "";
  export let exitButton = "";

  let displayVideo = false;

  const dispatch = createEventDispatcher();

  function exit() {
    resetModel();
    model.autoRotate = true;
    dispatch("close-description", false);
  }

  function resetModel() {
    model.cameraOrbit = "33.36deg 85.73deg 120m";
  }

  function openVideo() {
    displayVideo = true;
  }
</script>

<style type="text/scss">.feature-description {
  text-align: left;
  border-radius: 9px;
  width: 100%;
  max-width: 650px;
  display: flex;
  height: 95%; }
  .feature-description .layer {
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.4;
    position: absolute;
    top: 0em;
    border-radius: 9px;
    z-index: -1; }
  .feature-description .body {
    width: 100%;
    height: 100%;
    padding: 2px;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    flex-direction: column; }
    .feature-description .body .exit {
      width: 1.5rem;
      position: absolute;
      right: 0;
      margin-right: 5px;
      margin-top: 5px; }
    .feature-description .body .title {
      color: #15b6eb;
      font-size: 1rem; }
    .feature-description .body .subtitle {
      font-size: 0.8rem; }
    .feature-description .body .header {
      width: 85%;
      margin-top: 0.8rem; }
  .feature-description .video-container {
    width: 72%;
    margin: auto; }
  .feature-description .thumbnail {
    width: 100%;
    height: 100%;
    min-height: 100px;
    background-image: url("https://players.brightcove.net/5226411160001/default_default/index.html?videoId=6199924554001"); }
</style>

<div class="feature-description">
  <div class="body">
    <div class="exit" on:click={exit}>
      <img class="img" src={exitButton} alt="exit" />
    </div>
    <div class="header">
      <p class="title">
        {@html title}
      </p>
      <p class="subtitle">
        {@html subtitle}
      </p>
    </div>
    <div class="video-container">
      <div class="thumbnail" on:click={openVideo}>
        <img class="img" src={thumbnail} alt="video" />
      </div>
      {#if displayVideo}
        <VideoPlayer
          dataAccount={videoAccount}
          {videoId}
          {exitButton}
          on:close-description={exit} />
      {/if}
    </div>
    <div class="layer" />
  </div>
</div>
