<script>
  import { onMount } from "svelte";
  export let zoomifyGlassFront;
  export let zoomifyGlass;

  onMount(() => {
    animationZoomifyGlass();
  });

  function animationZoomifyGlass() {
    let zoomScreenTL = new TimelineMax();
    zoomScreenTL
      .add("reset")
      .from(
        glassContainer,
        0.3,
        { opacity: 0, scaleX: 0, scaleY: 0, y: "120" },
        0.3
      )
      .to(glassContainer, 0.3, { scaleX: 1.2, scaleY: 1.2 }, 0.3)
      .to(
        glassContainer,
        0.3,
        { opacity: 1, scaleX: 1, scaleY: 1, y: "0" },
        0.4
      )
      .add("slide")
      .to(glassContainer, 3, { x: "-50" }, "slide")
      .to(EV_Inmersive, 3, { x: "50" }, "slide");
  }
</script>

<style>
  #glassContainer {
    position: absolute;
    width: 41px;
    height: 48px;
    display: flex;
  }

  #EV_Inmersive {
    width: 140px;
    top: -10px;
    position: absolute;
    left: -87px;
  }

  #InmersiveContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-border-radius: 65px;
    -moz-border-radius: 65px;
    border-radius: 65px;
    overflow: hidden;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
  }
  .overlaysFXs {
    background-color: transparent;
    position: absolute;
    z-index: 1;
    top: 8%;
    left: 61%;
  }

  #EV_lens {
    position: absolute;
    height: 135%;
    left: -27px;
    top: -6px;
    width: 85px;
  }
</style>

<scipt />
<div
  id="animZoom"
  class="noMouseInteraction overlaysFXs"
  data-visibility-attribute="visible">
  <div id="glassContainer">
    <div id="InmersiveContainer">
      <img id="EV_Inmersive" src={zoomifyGlassFront} alt="EV_Inmersive" />
    </div>
    <img id="EV_lens" src={zoomifyGlass} alt="EV_lens" />
  </div>
</div>
