<script>
  import { onMount } from "svelte";
  import { createEventDispatcher } from "svelte";

  export let modelViewer = "";
  export let model = "";
  export let lights = "";
  export let background = "";
  export let checkmark = "";

  let modelTime = 0;
  const dispatch = createEventDispatcher();

  onMount(() => {
    //console.log("the component has mounted");
  });

  function onLoad() {
    dispatch("model-visible", true);
  }

  function openDescription(key) {
    dispatch("open-description", key);
  }

  (() => {
    let interval = self.setInterval(() => {
      modelTime = modelViewer.currentTime;
      if (
        modelViewer.animationName === "Running" &&
        modelViewer.currentTime >= 2
      ) {
        modelViewer.pause();
        clearInterval(interval);
      }
    }, 100);
  })();
</script>

