  z-index: 1; }

.model {
  background-repeat: no-repeat !important;
  background-position: center 75% !important;
  background-size: 95% 70% !important;
  display: block;
  text-align: center;
  margin: 0 auto;
  height: 100%;
  width: 100%; }

.hotspot {
  --min-hotspot-opacity: 0;
  border-radius: 32px;
  border: 0;
