  height: 24px;
  position: relative;
  transition: opacity 0.3s;
  width: 24px;
  background-size: contain !important; }

.hotspot .circle {
  display: inline-block;
  width: 27px;
  height: 27px;
  border-radius: 50%;
  background-color: #1196cc;
  transform: scale(2);
  opacity: 0.1;
  position: absolute;
  top: -2px;
  left: -2px;
  z-index: -1; }

.hotspot .scale-in-center {
  animation: scale-in-center 1.6s cubic-bezier(0.25, 0.5, 0.5, 0.94) infinite; }

@keyframes scale-in-center {
  0% {
    transform: scale(0);
    opacity: 1; }
  60% {
    opacity: 1; }
  100% {
    transform: scale(1.1);
    opacity: 0.1; } }

#unit01 {
  z-index: 1; }
</style>

<div id="pageWrapper">
  <div class="ModeViewerHolder">
    <model-viewer
      id="unit01"
      src={model}
      class="model"
      environment-image={lights}
